.buttons {
    display: flex;
    align-items: center;
    span {
        display: block;
        cursor: pointer;
        opacity: 0.5;
        height: 12px;
        margin-left: 15px;
        &:hover {
            opacity: 1; }
        &:first-child {
            margin-left: 0; } } }

.vk {
    width: 22px;
    background: url('./assets/vk.svg'); }

.fb {
    width: 6px;
    background: url('./assets/fb.svg'); }

.twitter {
    width: 15px;
    background: url('./assets/twitter.svg'); }

.telegram {
    width: 14px;
    background: url('./assets/telegram.svg'); }

.email {
    width: 17px;
    background: url('./assets/email.svg'); }

.ok {
    width: 7px;
    background: url('./assets/ok.svg'); }

.whatsapp {
    width: 12px;
    background: url('./assets/whatsapp.svg'); }
